
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Raleway:400,700");
body {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  background-color: #F6F6F6;
  font-size: 16px;
  font-family: "Poppins";
  color: #242424;
  -webkit-font-smoothing: antialiased;
  position: relative;
}
@media (min-width: 800px) {
body {
      font-size: 18px;
}
}
@media (min-width: 1200px) {
body {
      font-size: 20px;
}
}
* {
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
h1 {
  font-weight: 600;
  margin: 0;
}
h2 {
  margin: 0;
}
.About {
  width: 100%;
  height: 100vh;
  background-color: #F6F6F6;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  overflow: hidden;
}
.About::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: #6E00FF;
    z-index: 2;
}
.About::before {
    content: "about";
    position: absolute;
    font-size: 550px;
    font-weight: 700;
    color: rgba(36, 36, 36, 0.07);
    z-index: 0;
    pointer-events: none;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
@media (max-width: 1420px) {
.About::before {
      font-size: 450px;
}
}
@media (max-width: 1200px) {
.About::before {
      font-size: 350px;
}
}
@media (max-width: 800px) {
.About::before {
      font-size: 250px;
}
}
@media (max-width: 500px) {
.About::before {
      font-size: 120px;
}
}
.About .Logo {
    position: absolute;
    top: 0;
    right: 0;
    margin: 35px 20px;
}
.About .Logo img {
      display: block;
      width: 50px;
      height: 50px;
      -o-object-fit: contain;
         object-fit: contain;
}
@media (max-width: 800px) {
.About .Logo img {
          width: 30px;
          height: 30px;
}
}
.About .Section-Wrapper {
    max-width: 1000px;
    margin: 0 auto;
    padding: 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.About .Section-Wrapper > span {
      font-weight: 600;
      color: rgba(36, 36, 36, 0.35);
      display: block;
}
.About h1 {
    font-size: 70px;
    color: #242424;
    margin: 0;
}
@media (max-width: 1200px) {
.About h1 {
        font-size: 50px;
}
}
@media (max-width: 800px) {
.About h1 {
        font-size: 35px;
}
}
@media (max-width: 500px) {
.About h1 {
        font-size: 30px;
}
}
.About h1 span {
      display: inline-block;
}
.About h1 span.ClearCanvas {
        cursor: pointer;
        color: #00b2ec;
}
.About h2 {
    margin-top: 50px;
    font-size: 36px;
    font-weight: 600;
}
@media (max-width: 1200px) {
.About h2 {
        font-size: 30px;
}
}
@media (max-width: 800px) {
.About h2 {
        font-size: 25px;
}
}
@media (max-width: 500px) {
.About h2 {
        font-size: 20px;
}
}
.About h2 a,
    .About h2 span {
      display: inline-block;
}
.About h2 a.Lumencase, .About h2 a.Slected, .About h2 a.Taxifusion,
      .About h2 span.Lumencase,
      .About h2 span.Slected,
      .About h2 span.Taxifusion {
        cursor: pointer;
}
.About h2 a.Taxifusion,
      .About h2 span.Taxifusion {
        color: #FFBE00;
}
.About h2 a.Lumencase,
      .About h2 span.Lumencase {
        color: #F6416C;
}
.About h2 a.Slected,
      .About h2 span.Slected {
        color: #1852A4;
}
@media (max-width: 800px) {
.About .Bg-Line {
      background-color: rgba(81, 33, 230, 0.07);
}
}
@media (max-width: 500px) {
.About .Bg-Line {
      background-color: rgba(81, 33, 230, 0.07);
}
}



