
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Raleway:400,700");
body {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  background-color: #F6F6F6;
  font-size: 16px;
  font-family: "Poppins";
  color: #242424;
  -webkit-font-smoothing: antialiased;
  position: relative;
}
@media (min-width: 800px) {
body {
      font-size: 18px;
}
}
@media (min-width: 1200px) {
body {
      font-size: 20px;
}
}
* {
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
h1 {
  font-weight: 600;
  margin: 0;
}
h2 {
  margin: 0;
}
.Bg-Line {
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  z-index: 1;
  background-color: rgba(81, 33, 230, 0.1);
}
.Bg-Line:nth-child(1) {
  left: calc(1 * 12.5vw - 1px);
}
.Bg-Line:nth-child(2) {
  left: calc(2 * 12.5vw - 1px);
}
.Bg-Line:nth-child(3) {
  left: calc(3 * 12.5vw - 1px);
}
.Bg-Line:nth-child(4) {
  left: calc(4 * 12.5vw - 1px);
}
.Bg-Line:nth-child(5) {
  left: calc(5 * 12.5vw - 1px);
}
.Bg-Line:nth-child(6) {
  left: calc(6 * 12.5vw - 1px);
}
.Bg-Line:nth-child(7) {
  left: calc(7 * 12.5vw - 1px);
}



